@use "global" as *;

.sub-notfound {
  margin-top: rem(80);
  margin-bottom: rem(120);
  text-align: center;
  @include mq(md) {
    margin-top: rem(150);
    margin-bottom: rem(150);
  }
}

.sub-notfound__title {
  font-size: rem(40);
  color: $yellow;
  opacity: 0.3;
  font-weight: normal;
  @include mq(md) {
    font-size: rem(60);
  }
}

.sub-notfound__text {
  margin-top: rem(80);
}

.sub-notfound__gototop {
  margin-top: rem(80);
  @include mq(md){
    margin: rem(80) auto 0;
}
}

